body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

li {
  list-style: none; }

ul {
  padding: 0; }

@media screen and (max-width: 1000px) {
  .web-root .slider-box {
    display: none; }
  .web-root .content-box {
    width: 100%; }
  .web-root .header-container .blogger-title {
    display: none; } }

@media screen and (max-width: 548px) {
  .ant-tag {
    font-size: 15px !important; }
  .web-root {
    font-size: 16px !important; }
    .web-root .header-container .ant-col-5 {
      text-align: center;
      width: 100%; }
      .web-root .header-container .ant-col-5 .logo {
        margin: 12px 0;
        height: 35px;
        line-height: 35px;
        display: inline-block; }
      .web-root .header-container .ant-col-5 .blogger-title {
        display: block; }
    .web-root .header-container .ant-col-7 {
      display: none; }
    .web-root .header-container .ant-col-4 {
