      height: 59px;
      line-height: 59px;
      position: absolute;
      top: 0px;
      right: 0px; }
      .web-root .header-container .ant-col-4 .userHead {
        text-align: center;
        margin: 0; }
        .web-root .header-container .ant-col-4 .userHead .ant-avatar {
          margin: 0 !important;
          background-color: transparent; }
          .web-root .header-container .ant-col-4 .userHead .ant-avatar .anticon {
            color: #777777; }
    .web-root .header-container .ant-col-8 {
      width: 59px;
      height: 59px;
      position: absolute;
      top: 0px;
      left: 0px; }
      .web-root .header-container .ant-col-8 .ant-menu-horizontal {
