      .ant-menu-submenu-popup .ant-menu .ant-menu-item {
        height: 56px;
        line-height: 56px;
        font-size: 16px; }
        .ant-menu-submenu-popup .ant-menu .ant-menu-item .anticon {
          font-size: 16px; } }

